@import url(../../../node_modules/normalize-css/normalize.css);
html,
body,
div,
span,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
address,
img,
small,
strong,
sub,
sup,
b,
i,
ol,
ul,
li,
form,
label,
article,
aside,
footer,
header,
nav,
section,
time,
audio,
video {
  margin: 0;
  outline: 0;
  border: 0;
  padding: 0;
  background: transparent;
}

html {
  word-break: normal;
}

body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  scroll-behavior: smooth;
}

ul[class],
ol {
  list-style: none;
}

a {
  outline: 0;
  text-decoration: none;
  background: transparent;
  text-decoration-skip-ink: none;
}

address {
  font-style: normal;
}

input,
button,
textarea,
select {
  vertical-align: middle;
  margin: 0;
  border: none;
  border-radius: 0;
  padding: 0;
  font: inherit;
  color: inherit;
  background: transparent;
}

img {
  display: block;
  max-width: 100%;
}

:focus {
  outline: none;
}

body {
  font-family: "DMSans-Regular", sans-serif;
  font-weight: 400;
}

@font-face {
  src: url("../fonts/DMSans-Regular.woff2") format("woff2"), url("../fonts/DMSans-Regular.woff") format("woff");
  font-family: "DMSans-Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  src: url("../fonts/DMSans-Bold.woff2") format("woff2"), url("../fonts/DMSans-Bold.woff") format("woff");
  font-family: "DMSans-Bold";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-size: 16px;
  line-height: 1.5;
  color: #585858;
}

.wrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.main {
  flex-grow: 1;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1230px;
}

.up {
  position: fixed;
  right: 30px;
  bottom: 30px;
  z-index: 999;
  display: none;
  width: 32px;
  height: 32px;
  cursor: pointer;
}

.up--animated {
  animation-name: "up";
  animation-duration: 2s;
  animation-timing-function: ease;
  animation-iteration-count: infinite;
}
